import axios from "axios";
import apiAuth from "@/apis/auth";

//게시물 목록 요청 //통신 모듈처럼사용된다
async function getBoardList(pageNo = 1) {
    let response = null;
    try {
        response = await axios.get("/board/list", { params: { pageNo: pageNo } });
        return { result: "success", data: response.data }; //데이터에 결과 값을 저장한다.
        // console.log(response);
    } catch (error) {
        console.log(error);
        if (error.response) {
            //서버 응답이 있다.
            if (error.response.status === 403) {
                if (await apiAuth.refreshToken()) { //또한 보안을 위해서 접근 토큰은 짧게, 리플래쉬는 자주 하여 반복한다. 이를 통해 노출, 탈취가 되더라도 예방
                    //설령 만료가 되었더라도 아직 로그인 상태라면 시간 상관 없이 접근 할 수 있도록 사용자 모르게 언제나 리플레시ㅟ 진행
                    response = await axios.get("/board/list", { params: { pageNo: pageNo } });
                    return { result: "success", data: response.data }; //데이터에 결과 값을 저장한다.
                }
            }
        } else {
            //서버 응답이 없다. 즉 네트워크 오류
            return { result: "fail" };
        }
    }
    if (response != null) {
        //json 리턴의 값이 data이다. 그 뭐냐 vue의 data값이다.
        return response.data;
    } else {
        return null;
    }
}

//게시물 쓰기
// 게시물 쓰기 (멀티 파트)
async function createBoard(multipartFormData) {
    let dbBoard = null;
    try {
        const response = await axios.post("/board/", multipartFormData);
        dbBoard = response.data;
    } catch (error) {
        if (error.response) {
            //서버 응답이 있다.
            if (error.response.status === 403) {
                if (await apiAuth.refreshToken()) {
                    const response = await axios.post("/board/", multipartFormData);
                    dbBoard = response.data;
                }
            }
        }
    }

    return dbBoard;
}

//게시물 가져오기
async function readBoard(bno, hit) {
    let board = null;
    try {
        const response = await axios.get(`/board/${bno}?hit=${hit}`);
        board = response.data;
    } catch (error) {
        if (error.response) {
            //서버 응답이 있다.
            if (error.response.status === 403) {
                if (await apiAuth.refreshToken()) {
                    const response = await axios.get(`/board/${bno}?hit=${hit}`);
                    board = response.data;
                }
            }
        }
    }

    return board;
}

//파일 다운로드
async function downloadBoardAttach(bno) {
    let blob = null;

    try {
        const response = await axios.get(`/board/battach/${bno}`, { responseType: "blob" }); //blob타입의 데이터 타입이 들어가야 동작한다.는 조건
        blob = response.data;
    } catch (error) {
        if (error.response) {
            //서버 응답이 있다.
            if (error.response.status === 403) {
                if (await apiAuth.refreshToken()) {
                    const response = await axios.get(`/board/battach/${bno}`, { responseType: "blob" }); //blob타입의 데이터 타입이 들어가야 동작한다.는 조건
                    blob = response.data;
                }
            }
        }
    }

    return blob;
}

//게시글 삭제
async function deleteBoard(bno) {
    let result = null;

    try {
        const response = await axios.delete(`/board/${bno}`);
        result = response.data.result; //spring에서  result라는 값으로 넘어가기 때문에
    } catch (error) {
        if (error.response) {
            //서버 응답이 있다.
            if (error.response.status === 403) {
                if (await apiAuth.refreshToken()) {
                    const response = await axios.delete(`/board/${bno}`);
                    result = response.data.result; //spring에서  result라는 값으로 넘어가기 때문에
                }
            }
        }
    }

    return result;
}

async function updateBoard(multipartFormData) {
    let dbBoard = null;

    try {
        const response = await axios.put(`/board/`, multipartFormData);
        dbBoard = response.data;
    } catch (error) {
        if (error.response) {
            //서버 응답이 있다.
            if (error.response.status === 403) {
                if (await apiAuth.refreshToken()) {
                    const response = await axios.put(`/board/`, multipartFormData);
                    dbBoard = response.data;
                }
            }
        }
    }
    return dbBoard;
}

//내보내기
export default {
    getBoardList,
    createBoard,
    readBoard,
    downloadBoardAttach,
    deleteBoard,
    updateBoard,
};
